/**
 * Forms
 */

button,
input,
optgroup,
select,
textarea {
    font-family: inherit; // Override normalize.css rule
}

form {
    @include paragraph;
}

*:disabled {
    opacity: 0.7 !important;
    cursor: not-allowed !important;
}

input,
textarea,
select,
button,
label {
    @include inline-block;

    width: 100%;
}

input,
textarea,
select,
button {
    border-radius: 3px;
    border: $jentil-separator;
    padding: 5px 7px;
    margin-bottom: 4px;
}

input,
textarea,
select {
    background-color: #fff;

    &:focus {
        border-color: #ccc;
    }
}

input,
button {
    overflow: visible; /* IE6 fix */
}

input {
    &[type=checkbox],
    &[type=radio] {
        margin: auto 5px;
        width: auto;
        vertical-align: middle;
    }
}

input[type=submit],
input[type=button],
input[type=reset],
button {
    width: auto;
    height: auto;
    cursor: pointer;
    border: 0;
    padding: 7px 10px 8px;
    font-weight: bold;
    font-size: map-get($jentil-font-size, tiny);
    line-height: 100%;
    text-align: center;
    background-color: #ddd;

    &:focus {
        border-width: 0;
    }
}

/// Fix button background color not showing in opera mini
/// @link https://github.com/csswizardry/inuit.css/issues/170#issuecomment-14859371
.opera:-o-prefocus,
input[type=submit],
input[type=button],
input[type=reset],
button {
    border-right: 1px solid rgba(0,0,0,0.0);
}

label {
    cursor: pointer;
    font-size: map-get($jentil-font-size, tiny);

    &[for] {
        width: auto;
    }

    > * {
       font-size: map-get($jentil-font-size, small);
    }
}

/** Search form */

.form.search {
    @include position(relative);

    input[type=search] {
        margin: 0;
        padding: 6px 8px;
    }

    button {
        @include position(absolute, 2px 2px null null);

        margin-bottom: 0;
        padding: 7px 10px;
        border: 0;
        font-size: 12px;
        z-index: 1;
    }
}
