/**
 * Posts
 */

.archive-description {
    @include paragraph(10px);

    color: map-get($jentil-font-color, lighter);
}

.entry-content,
.pagination {
    @include paragraph;
    @include clearfix;
}

/** Page Header */

.page-header {
    @include paragraph;
    @include clearfix;

    .archive & .avatar {
        margin-right: 15px;
    }

    .page-title {
        @include paragraph(10px);

        color: map-get($jentil-font-color, lighter);
        font-size: 22px;
    }

    .singular-post.big & {
        .entry-meta {
            line-height: map-get($jentil-line-height, normal);

            p {
                margin: 0;

                .avatar {
                    float: left;
                    margin-right: 10px;
                }
            }
        }

        .entry-title {
            font-size: 29px;
        }
    }
}

/** Attachment */

.parent.entry-title {
    font-size: map-get($jentil-font-size, small);
}

.attachment {
    .image-pagination {
        .prev {
            float: left;
        }

        .next {
            float: right;
        }
    }

    .entry-caption {
        text-align: center;
        font-size: map-get($jentil-font-size, small);
        line-height: map-get($jentil-line-height, normal);
        margin-top: 10px;
    }
}

.entry-attachment {
    text-align: center;

    &.image {
        .image-wrap {
            background-color: #000;
            line-height: 1px;
        }
    }
}

/** Sticky posts */

.sticky-posts {
    @include paragraph;

    border-radius: 3px;
    background-color: $jentil-text-background;
    padding: 20px;
}

/** Related posts */

#related-posts-wrap {
    @include paragraph;
}

/** Avatar */

.avatar {
    float: left;
    border-radius: 50%;
    margin-right: 10px;
}

@media (min-width: map-get($jentil-breakpoint, small)) {
    /** Sticky posts */

    .sticky-posts {
        &.layout-grid {
            padding: 12px;
            margin: 0;

            @include paragraph;
        }
    }
}

@media (min-width: map-get($jentil-breakpoint, medium)) {
    .page-header {
        .singular-post.big & {
            .entry-title {
                font-size: 34px;
            }
        }
    }

    .layout-grid.posts-wrap {
        > .post-wrap {
            .layout-columns-3 & {
                width: 50%;
            }
        }
    }
}
